import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeHeaderModule } from 'src/app/clients/pages/home/home-header/home-header.module';
import { HomeComponent } from './home.component';

import { SimpleGuard } from 'src/app/shared/guards/simple.guard';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
  },
  
  {
    path: 'login',
    loadChildren: () =>
      import('src/app/clients/pages/login/login.module').then(
        (m) => m.LoginModule
      ),
  },
  {
    path: 'yeu-cau-su-dung-ptn',
    loadChildren: () =>
      import(
        'src/app/clients/pages/home/yeu-cau-su-dung-ptn/yeu-cau-su-dung-ptn.module'
      ).then((m) => m.YeuCauSuDungPTNModule),
  },
  {
    path: 'index',
    // canActivate: [SimpleGuard],
    loadChildren: () =>
      import('src/app/clients/pages/main/main.module').then(
        (m) => m.MainNavModule
      ),
  },
  {
    path: 'danh-sach-sinh-vien',
    loadChildren: () =>
      import(
        'src/app/clients/pages/home/danh-sach-sinh-vien/danh-sach-sinh-vien.module'
      ).then((m) => m.DanhSachSinhVienModule),
  },
  {
    path: 'thiet-bi',
    loadChildren: () =>
      import('src/app/clients/pages/home/dashboard/dashboard.module').then(
        (m) => m.DashboardModule
      ),
  },
  {
    path: 'yeu-cau-muon-thiet-bi',
    loadChildren: () =>
      import(
        'src/app/clients/pages/home/yeu-cau-muon-thiet-bi/yeu-cau-muon-thiet-bi.module'
      ).then((m) => m.YeuCauMuonThietBiModule),
  },
  {
    path: 'lien-he',
    loadChildren: () =>
      import(
        'src/app/clients/pages/home/lien-he-qua-email/lien-he-qua-email.module'
      ).then((m) => m.LienHeQuaEmailModule),
  },
];

@NgModule({
  declarations: [HomeComponent],
  imports: [CommonModule, RouterModule.forChild(routes), HomeHeaderModule],
  exports: [RouterModule],
})
export class HomeModule {}
